import { Component } from '@angular/core';
import { YoutubeSafeUrlPipe } from '../../pipes/youtube-safe-url.pipe';
import { PlaylistState } from '../../services/playlist-state.service';
import { Playlist } from '../models/playlist.model';


@Component({
    selector: 'my-player',
    template: require('./player.component.html'),
    styles: [require('./player.component.css')]
})

export class PlayerComponent {
    constructor( public playlistState : PlaylistState ) {}
    onStateChange(event) {
    if(event.data === 0) { this.nextVideo(); }
    }
    savePlayer(player) {
        this.playlistState.player = player;
        player.playVideo()
    }
    nextVideo() {
if(this.playlistState.activeVideoPosition + 1 === this.playlistState.playListSize) { this.playlistState.activeVideoPosition = 0; }
else { this.playlistState.activeVideoPosition = this.playlistState.activeVideoPosition + 1; }
        console.log(this.playlistState.activeVideoPosition);
        this.playlistState.activeVideo = this.playlistState.playList[this.playlistState.activeVideoPosition];
        this.playlistState.player.loadVideoById(this.playlistState.playList[this.playlistState.activeVideoPosition]._id);

    }
}



