import { Injectable } from "@angular/core";
import { Playlist } from "../components/models/playlist.model";
import { Subject }    from "rxjs/Subject";

@Injectable()
export class PlaylistState {
    
    playListFilled : Subject<string> = new Subject<string>();
    playList : Playlist[] = [];
    playListSize : number;
    activeVideo : Playlist;
    activeVideoPosition : number;
    player : any;
}



